.Uploads {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
}

.form-container,
.catch-of-week-container {
    width: 40%;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-top: 20px;
}

.form-container h2,
.catch-of-week-container h2 {
    margin-bottom: 20px;
    text-align: center;
}

form,
.catch-of-week-container form {
    display: flex;
    flex-direction: column;
}

form label,
.catch-of-week-container form label {
    margin-bottom: 10px;
}

form input,
.catch-of-week-container form input,
form select,
.catch-of-week-container form select {
    margin-bottom: 15px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

form button,
.catch-of-week-container form button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

form button:hover,
.catch-of-week-container form button:hover {
    background-color: #0056b3;
}
